/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react'
import {useTranslation} from "react-i18next";


export const Header = () => {
    const [show, setShow] = useState(false);

    const toggleShow = () => {
        setShow(!show)
    }

    const {t, i18n} = useTranslation('common');

    return (
        <header className="header" >
            <div className="logo">
                <p className="name text-header">Ing. Emanuel Torres C.</p>
            </div>
            <button className={show ? 'nav__open nav-toggle' : 'nav-toggle'} onClick={toggleShow} aria-label="toggle navigation">
                <span className="hamburger" ></span>
            </button>
            <nav className={show ? 'nav__open nav' : 'nav'}>
                <ul className="nav__list">
                    <li className="nav__item">
                        <a href="#home" className="nav__link" onClick={toggleShow}>{t('navigation.home')}</a>
                    </li>
                    <li className="nav__item">
                        <a href="#services" className="nav__link" onClick={toggleShow}>{t('navigation.services')}</a>
                    </li>
                    <li className="nav__item">
                        <a href="#about" className="nav__link" onClick={toggleShow}>{t('navigation.about')}</a>
                    </li>
                    <li className="nav__item">
                        <a href="#work" className="nav__link" onClick={toggleShow}>{t('navigation.work')}</a>
                    </li>
                    <li className="nav__item" onClick={toggleShow} >
                        <a className="nav__link" onClick={() => i18n.changeLanguage('es')}>Es</a>
                        <span className="nav__link">/</span>
                        <a className="nav__link" onClick={() => i18n.changeLanguage('en')}>En</a>
                    </li>
                </ul>
            </nav>
        </header>
    )
}
